<template>
  <div class="home">
    <SelectCountry></SelectCountry>
    <HeadTemplate></HeadTemplate>
    <IntroductionTemplate></IntroductionTemplate>
    <InformationTemplate></InformationTemplate>
    <ProgramTemplate></ProgramTemplate>
    <QATemplate></QATemplate>
    <FooterTemplate></FooterTemplate>
  </div>
</template>

<script>
import HeadTemplate from "@/components/home/head/Index.vue";
import IntroductionTemplate from "@/components/home/introduction/Index.vue";
import InformationTemplate from "@/components/home/information/Index.vue";
import ProgramTemplate from "@/components/home/program/Index.vue";
import QATemplate from "@/components/home/qa/Index.vue";
import FooterTemplate from "@/components/home/footer/Index.vue";
import SelectCountry from "@/components/SelectCountry.vue";
export default {
  name: "Home",
  components: {
    HeadTemplate,
    IntroductionTemplate,
    InformationTemplate,
    ProgramTemplate,
    QATemplate,
    FooterTemplate,
    SelectCountry
  },
  computed: {
    
  },
  data() {
    return {
      user: this.$store.state.user,
      factory: this.$store.state.factory,
      gameId: 0,
      newSageRouter: this.$store.state.newSageRouter,
      newsage: this.$store.state.newsage,
      newToke:this.$store.state.newToke,
    };
  },
  created() {
    let that = this;
    that.displayLoding({ isloading: true });
    this.$store.dispatch(ACTION_TYPES.REGISTER_WEB3_INSTANCE).then(() => {
      that.getFactory().then(() => {
        that.initRouterdata().then(() => {
         
          Promise.all([
            that.initMaxTeamTeamId(),
            that.randomDisplay(),
            that.getNewToken(),
          ]).then(() => {
            
                that.displayLoding({ isloading: false });
          });
        });
          
      });
    });
  },
  watch: {},
  methods: {
    ...mapActions([
      ACTION_TYPES.INIT_NEWTOKEN,
      ACTION_TYPES.INIT_NEWSAGE,
      ACTION_TYPES.INIT_FACTORY,
      ACTION_TYPES.INIT_NEWSAGE_ROUTER,
      "displayLoding",
      "randomDisplayData"
    ]),
    CallSmartMatrixNewsage(obj) {
      const that = this;
      return new Promise(function (resolve, reject) {
        that.$root.callToAccessBlockchain({
          value: obj.value, //转账值
          requestParams: obj.requestParams, //参数
          contractIndexToUse: obj.contractIndexToUse, //合约名称
          methodName: obj.methodName, //方法名称
          managerIndex: "NewsageManager",
          pairtType: obj.pairtType,
          callback: (isSuccess) => {
            isSuccess != undefined ? resolve(isSuccess) : reject();
          },
        });
      });
    },
    //游戏启动次数
    [FACTORY_TYPES.FACTORY_LAST_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_LAST_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //代币地址
    [FACTORY_TYPES.FACTORY_DETO_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_DETO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //路由工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //newsage工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //WETH地址
    [FACTORY_TYPES.FACTORY_WETH_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_WETH_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 资金池地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 路由地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //历史游戏地址查询 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_FACTORY_GAMES](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: { id },
        methodName: FACTORY_TYPES.EVENT_FACTORY_GAMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //当前游戏配置信息 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    getFactory() {
      const that = this;
      let factory = that.factory;
      const promises = [
        //游戏启动次数
        that[FACTORY_TYPES.FACTORY_LAST_ID](),
        //代币地址
        that[FACTORY_TYPES.FACTORY_DETO_ADDRESS](),
        //路由工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS](),
        //newsage工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS](),
        //WETH地址
        that[FACTORY_TYPES.FACTORY_WETH_ADDRESS](),
        //uniswap 资金池地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS](),
        //uniswap 路由地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS](),
        //当前游戏配置信息 获得路由地址 和 游戏地址
        that[FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS](),
      ];
      return new Promise(function (resolve, reject) {
        Promise.all(promises).then((arr) => {
          factory.lastId = Number(arr[0]);
          factory.DETOAddress = arr[1];
          factory.NewSageRouterFactory = arr[2];
          factory.SmartMatrixNewsageFactory = arr[3];
          factory.WETHAddress = arr[4];
          factory.uniswapV2PairAddress = arr[5];
          factory.uniswapV2Router02Address = arr[6];
          factory.SmartMatrixNewsageAddress =
            arr[7]["SmartMatrixNewsageAddress"];
          factory.NewSageRouterAddress = arr[7]["NewSageRouterAddress"];
          that[ACTION_TYPES.INIT_FACTORY]({ factory });
          arr ? resolve(arr) : reject();
        });
      });
    },
    //历史游戏地址查询 获得路由地址 和 游戏地址
    getGame() {
      const that = this;
      let gameId = Number(that.gameId);
      ////历史游戏地址查询 获得路由地址 和 游戏地址
      that[FACTORY_TYPES.EVENT_FACTORY_GAMES](gameId).then((res) => {
        that.selectGamems = res;
        
      });
    },
    //========================================================
    //代币地址
    [NEWSAGE_ROUTER_TYPES.ROUTER_DETO_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_DETO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //本轮最近一次回购时间
    [NEWSAGE_ROUTER_TYPES.ROUTER_HUIGOU_TIMES]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_HUIGOU_TIMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //最近投注时间
    [NEWSAGE_ROUTER_TYPES.ROUTER_XIAZHU_TIMES]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_XIAZHU_TIMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //倒计时
    [NEWSAGE_ROUTER_TYPES.ROUTER_OVER_TIMES]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_OVER_TIMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //倒计时结束后投注的次数,每次减少1秒
    [NEWSAGE_ROUTER_TYPES.ROUTER_OVER_CS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_OVER_CS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //游戏结束,超过了倒计时
    [NEWSAGE_ROUTER_TYPES.ROUTER_IS_GAMEOVE]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_IS_GAMEOVE,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //目标总标金额,到达该金额时启动倒计时
    [NEWSAGE_ROUTER_TYPES.ROUTER_THRESHOLD]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_THRESHOLD,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //当前总投注额
    [NEWSAGE_ROUTER_TYPES.ROUTER_TOTAL_AMOUNTS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_TOTAL_AMOUNTS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //本轮总投回购注销deto
    [NEWSAGE_ROUTER_TYPES.ROUTER_DETO_BURN_TOTAL_AMOUNTS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_DETO_BURN_TOTAL_AMOUNTS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //本轮总投回购ETH
    [NEWSAGE_ROUTER_TYPES.ROUTER_ETH_BURN_TOTAL_AMOUNTS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_ETH_BURN_TOTAL_AMOUNTS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //工厂合约地址
    [NEWSAGE_ROUTER_TYPES.ROUTER_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap资金池地址
    [NEWSAGE_ROUTER_TYPES.ROUTER_UNISWAPV2_PAIR_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_UNISWAPV2_PAIR_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap路由地址
    [NEWSAGE_ROUTER_TYPES.ROUTER_UNISWAPV2_ROUTER02_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_UNISWAPV2_ROUTER02_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //path(uint256)//Token地址[WETH,NETO]
    [NEWSAGE_ROUTER_TYPES.EVENT_WETH_DETO_ADDRESS](ID) {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: { ID },
        methodName: NEWSAGE_ROUTER_TYPES.EVENT_WETH_DETO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //投注总次数
    [NEWSAGE_ROUTER_TYPES.ROUTER_PERBET_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_PERBET_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },

    //钻石雨执行回购
    [NEWSAGE_ROUTER_TYPES.EVENT_SWAP_DETO_BURN]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_ROUTER_TYPES.EVENT_SWAP_DETO_BURN,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //个人铸造币总和
    [NEWSAGE_ROUTER_TYPES.ROUTER_ADDRESS_TO_DETO_MINT_AMOUNTS](address){
      const that = this;
      let temObj = {
        contractIndexToUse: "NewSageRouter", //合约名称
        value: 0,
        requestParams: {address},
        methodName: NEWSAGE_ROUTER_TYPES.ROUTER_ADDRESS_TO_DETO_MINT_AMOUNTS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    getNextC() {
      const that = this;
      //回购
      that[NEWSAGE_ROUTER_TYPES.EVENT_SWAP_DETO_BURN]();
    },
    initRouterdata() {
      const that = this;
      let newSageRouter = that.newSageRouter;

      const promises = [
        //本轮最近一次回购时间
        that[NEWSAGE_ROUTER_TYPES.ROUTER_HUIGOU_TIMES](),
        //最近投注时间
        that[NEWSAGE_ROUTER_TYPES.ROUTER_XIAZHU_TIMES](),
        //倒计时
        that[NEWSAGE_ROUTER_TYPES.ROUTER_OVER_TIMES](),
        //游戏是否结束
        that[NEWSAGE_ROUTER_TYPES.ROUTER_IS_GAMEOVE](),
        //目标总标金额
        that[NEWSAGE_ROUTER_TYPES.ROUTER_THRESHOLD](),
        //当前总投注额
        that[NEWSAGE_ROUTER_TYPES.ROUTER_TOTAL_AMOUNTS](),
        //本轮总投回购注销deto数量
        that[NEWSAGE_ROUTER_TYPES.ROUTER_DETO_BURN_TOTAL_AMOUNTS](),
        //本轮总投回购ETH
        that[NEWSAGE_ROUTER_TYPES.ROUTER_ETH_BURN_TOTAL_AMOUNTS](),
        //perBetId逐笔投足次数
        that[NEWSAGE_ROUTER_TYPES.ROUTER_PERBET_ID](),
        //最后的用户id
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_USER_ID](),
        //团队总数
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_TEAM_ID](),
        //最大团队首领地址
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_FIND_MAX_TEAM](),
        //获取eth 兑dai 价格
        that[IUNISWAPV2PAIR.WETH_DAI_PAIR](),
        //获取eth 兑deto 价格
        that[IUNISWAPV2PAIR.WETH_DETO_PAIR](),
      ];

      return new Promise(function (resolve, reject) {
        Promise.all(promises).then((res) => {
          
          newSageRouter.blockTimesHuigou = new Date(Number(res[0]) * 1000);
          newSageRouter.blockTimesLast = new Date(Number(res[1]) * 1000);
          newSageRouter.overTimess = Number(res[2]);
          newSageRouter.isGameove = res[3];
          newSageRouter.threshold = that.$store.state.web3
            .instance()
            .utils.fromWei(res[4], "ether");
          newSageRouter.totalAmounts = that.$store.state.web3
            .instance()
            .utils.fromWei(res[5], "ether");
          newSageRouter.detoBurnTotalAmounts = that.$store.state.web3
            .instance()
            .utils.fromWei(res[6], "ether");
          newSageRouter.ethBurnTotalAmounts = that.$store.state.web3
            .instance()
            .utils.fromWei(res[7], "ether");
          newSageRouter.perBetId = Number(res[8]);
          newSageRouter.lastUserId = Number(res[9]);
          newSageRouter.lastteamId = Number(res[10]);
          newSageRouter.leaderAddress = res[11];
          newSageRouter.ETHPrice =
            Number(
              that.$store.state.web3
                .instance()
                .utils.fromWei(res[12].reserve0, "ether")
            ) /
            Number(
              that.$store.state.web3
                .instance()
                .utils.fromWei(res[12].reserve1, "ether")
            );
          newSageRouter.DETOPrice =
            (Number(
              that.$store.state.web3
                .instance()
                .utils.fromWei(res[13].reserve0, "ether")
            ) /
              Number(
                that.$store.state.web3
                  .instance()
                  .utils.fromWei(res[13].reserve1, "ether")
              )) *
            newSageRouter.ETHPrice;
            

          that[ACTION_TYPES.INIT_NEWSAGE_ROUTER]({ newSageRouter });
          
          res ? resolve(res) : reject();
        });
      });
    },
    //========================================================

    //最后的用户id
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_USER_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_USER_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //根据id查询地址
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: { id },
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },

    //最后的team id
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_TEAM_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_LAST_TEAM_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },

    //最后的max team leader
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_FIND_MAX_TEAM]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: {},
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_FIND_MAX_TEAM,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },

    initMaxTeamTeamId() {
      const that = this;
      //let newsageObj=that.newsage;
      let newSageRouter = that.newSageRouter;
      return new Promise(function (resolve) {
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS](
          newSageRouter.leaderAddress
        ).then((res) => {
          that[NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_TEAM](Number(res.teamId)).then(
            (res) => {
              newSageRouter.findMaxTeam.leader = res.leader;
              newSageRouter.findMaxTeam.totalAmount = that.$store.state.web3
                .instance()
                .utils.fromWei(res.totalAmount, "ether");
              newSageRouter.findMaxTeam.member = Number(res.member);
              that[ACTION_TYPES.INIT_NEWSAGE_ROUTER]({ newSageRouter });
                resolve();
              }
            );
          });
      });
    },

    //========================================================
    //获取Uniswap 价格换算
    [IUNISWAPV2PAIR.WETH_DAI_PAIR]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "IuniswapV2Pair", //合约名称
        value: 0,
        requestParams: {},
        methodName: WETH_DAI_PAIR.GETRESERVES,
        pairtType: "WETH_DAI_PAIR", //WETH_DAI_PAIR WETH_DETO_PAIR
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    [IUNISWAPV2PAIR.WETH_DETO_PAIR]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "IuniswapV2Pair", //合约名称
        value: 0,
        requestParams: {},
        methodName: WETH_DAI_PAIR.GETRESERVES,
        pairtType: "WETH_DETO_PAIR", //WETH_DAI_PAIR WETH_DETO_PAIR
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //========================================================
    //idToAddress
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_TEAM](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: { id },
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_TEAM,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //查询用户的信息
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS](Address) {
      const that = this;
