    getAddressObj(address, newsageObj) {
      //查询addres信息
      const that = this;

      let selectAddress = address;
      return new Promise((resolve) => {
           //查询用户是否注册
      that[NEWSAGE_TYPES.UPDATE_NEWSAGE_IS_USER_EXISTS](selectAddress).then(
        (res) => {
          newsageObj.isUserExists = res;
          if (res) {
            //查询用户的信息
            let UPDATE_NEWSAGE_USERS_PROMISE = new Promise((resolve) => {
              that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS](selectAddress).then(
                (res) => {
                  newsageObj.users = {
                    isTeam: res.isTeam,
                    teamId: Number(res.teamId),
                    id: Number(res.id),
                    referrer: res.referrer,
                    partnersCount: Number(res.partnersCount),
                    income: 0,
                    betAmount: 0,
                    address:selectAddress,
                  };
                  resolve();
                },
                (err) => {
                  console.log(err);
                  resolve();
                }
              );
            });
            //x3的12个档次是否开通
            let UPDATE_NEWSAGE_USERS_ACTIVE_X3_LEVELS_PROMISE = new Promise(
              (resolve) => {
                let forX3Level12 = (level, cb) => {
                  if (level <= 12) {
                    that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS_ACTIVE_X3_LEVELS](
                      selectAddress,
                      level
                    ).then(
                      (res) => {
                        newsageObj.usersActiveX3Levels["level" + level] = res;
                        if (res) {
                          level++;
                          forX3Level12(level, cb);
                        } else {
                          cb();
                        }
                      },
                      (err) => {
                        console.log(err);
                        cb();
                      }
                    );
                  } else {
                    cb();
                  }
                };
                forX3Level12(1, () => {
                 
                  //x3矩阵数据
                  let forX3_MATRIX = (level, cb) => {
                    if (
                      level <= 12 &&
                      newsageObj.usersActiveX3Levels["level" + level]
                    ) {
                     
                      that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS_X3_MATRIX](
                        selectAddress,
                        level
                      ).then(
                        (res) => {
                          newsageObj.usersX3Matrix["level" + level] = {
                            currentReferrer: res[0], //users[userAddress].x3Matrix[level].currentReferrer 当前推荐人
                            referrals: res[1], //users[userAddress].x3Matrix[level].referrals,  0 1 2 矩阵位置
                            blocked: res[2], //users[userAddress].x3Matrix[level].blocked, 是否矩阵已满锁定
                            reinvestCount: res[3] ? Number(res[3]) : 0, //复投次数
                            income: res[3]
                              ? Number(res[3]) *
                                  2 *
                                  newsageObj.levelPrice[`level${level}`] +
                                res[1].length *
                                  newsageObj.levelPrice[`level${level}`]
                              : res[1].length *
                                newsageObj.levelPrice[`level${level}`],
                          };
                          newsageObj.usersX3Matrix.income =
                            newsageObj.usersX3Matrix.income +
                            newsageObj.usersX3Matrix["level" + level].income;
                          if (res) {
                            level++;
                            forX3_MATRIX(level, cb);
                          } else {
                            cb();
                          }
                        },
                        (err) => {
                          console.log(err);
                          cb();
                        }
                      );
                    } else {
                      cb();
                    }
                  };
                  forX3_MATRIX(1, () => {
                   
                    resolve();
                  });
                });
              }
            );
            //x6的12个档次是否开通
            let UPDATE_NEWSAGE_USERS_ACTIVE_X6_LEVELS_PROMISE = new Promise(
              (resolve) => {
                let forX6Level12 = (level, cb) => {
                  if (level <= 12) {
                    that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS_ACTIVE_X6_LEVELS](
                      selectAddress,
                      level
                    ).then(
                      (res) => {
                        newsageObj.usersActiveX6Levels["level" + level] = res;
                        if (res) {
                          level++;
                          forX6Level12(level, cb);
                        } else {
                          cb();
                        }
                      },
                      (err) => {
                        console.log(err);
                        cb();
                      }
                    );
                  } else {
                    cb();
                  }
                };
                forX6Level12(1, () => {
                  //x6矩阵数据
                  let forX6_MATRIX = (level, cb) => {
                    if (
                      level <= 12 &&
                      newsageObj.usersActiveX6Levels["level" + level]
                    ) {
                      
                      that[NEWSAGE_TYPES.UPDATE_NEWSAGE_USERS_X6_MATRIX](
                        selectAddress,
                        level
                      ).then(
                        (res) => {
                          //
                          newsageObj.usersX6Matrix["level" + level] = {
                            currentReferrer: res[0], //users[userAddress].x6Matrix[level].currentReferrer 当前推荐人
                            firstLevelReferrals: res[1], //users[userAddress].x6Matrix[level].firstLevelReferrals,
                            secondLevelReferrals: res[2], //users[userAddress].x6Matrix[level].secondLevelReferrals,
                            blocked: res[3], //users[userAddress].x6Matrix[level].blocked, 是否矩阵已满锁定
                            closedPart: res[4], //锁定地址
                            reinvestCount: res[5] ? Number(res[5]) : 0, //复投次数
                            income: res[5]
                              ? Number(res[5]) *
                                  2 *
                                  newsageObj.levelPrice[`level${level}`] +
                                res[1].length *
                                  newsageObj.levelPrice[`level${level}`]
                              : res[1].length *
                                newsageObj.levelPrice[`level${level}`],
                          };
                          newsageObj.usersX6Matrix.income =
                            newsageObj.usersX6Matrix.income +
                            newsageObj.usersX6Matrix["level" + level].income;

                          if (res) {
                            level++;
                            forX6_MATRIX(level, cb);
                          } else {
                            cb();
                          }
                        },
                        (err) => {
                          console.log(err);
                          cb();
                        }
                      );
                    } else {
                      cb();
                    }
                  };
                  forX6_MATRIX(1, () => {
                   
                    resolve();
                  });
                });
              }
            );

            Promise.all([
              UPDATE_NEWSAGE_USERS_PROMISE,
              UPDATE_NEWSAGE_USERS_ACTIVE_X3_LEVELS_PROMISE,
              UPDATE_NEWSAGE_USERS_ACTIVE_X6_LEVELS_PROMISE,
              // ,
              // UPDATE_NEWSAGE_FIND_FREE_X3_REFERRER_PROMISE,
              // UPDATE_NEWSAGE_FIND_FREE_X6_REFERRER_PROMISE
            ]).then(() => {
       

              newsageObj.users.income =
                newsageObj.usersX3Matrix.income +
                newsageObj.usersX6Matrix.income;
              for(let level=1; level<=12;level++){
                if(newsageObj.usersActiveX3Levels[`level${level}`]){
                  newsageObj.users.betAmount=newsageObj.users.betAmount+newsageObj.levelPrice[`level${level}`];
                }
                if(newsageObj.usersActiveX6Levels[`level${level}`]){
                  newsageObj.users.betAmount=newsageObj.users.betAmount+newsageObj.levelPrice[`level${level}`];
                }
              }
              

              
              resolve();
            });
          } else {
          
           
            resolve();
          }
        },
        (err) => {
          console.log(err);
         
         
          resolve();
        }
      );
    
      });
     },
     getMindTokenbyAddress(address){
