      let tempInt;
      switch (arguments.length) {
        case 1:
          tempInt= parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          tempInt= parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          //或者 Math.floor(Math.random()*( maxNum - minNum + 1 ) + minNum );
          break;
        default:
          tempInt= 0;
          break;
      }
      return tempInt;
