  methods:{
    store_state_factory(name){
      let url ='';
      switch(name){
        case 'SmartMatrixNewsageAddress':
         url = 'https://ropsten.etherscan.io/address/'+this.$store.state.factory.SmartMatrixNewsageAddress;
          break;
        case 'DETOAddress':
         url =  'https://ropsten.etherscan.io/address/'+this.$store.state.factory.DETOAddress;
          break;  
        case 'NewSageRouterAddress':
         url =  'https://ropsten.etherscan.io/address/'+this.$store.state.factory.NewSageRouterAddress;
          break; 
        case 'Factory':
         url =  'https://ropsten.etherscan.io/address/'+this.HTML_HOME_FOOT[this.$store.state.language].DATA;
          break;  
        case 'NewSageRouterFactory':
         url =  'https://ropsten.etherscan.io/address/'+this.$store.state.factory.NewSageRouterFactory;
          break;    
        case 'SmartMatrixNewsageFactory':
         url =  'https://ropsten.etherscan.io/address/'+this.$store.state.factory.SmartMatrixNewsageFactory;
          break;
        case 'uniswapV2PairAddress':
         url =  'https://ropsten.etherscan.io/address/'+this.$store.state.factory.uniswapV2PairAddress;
          break;             


      }
      return url;

    }
  }

}
