<script>
export default {
  name: "Information", //系统信息
  computed: {},
  data() {
    return {
      HTML_HOME_INFORMATION,
      showarrow: `0.3333333333333333rem `,
      SELETE_SHOW_DATA_ID:3
    };
  },
  watch: {},
  mounted() {
    window.onresize = () => {
      this.GetWindowInfo();
    };
  },
  methods: {
    GetWindowInfo() {
      this.showarrow = (window.innerWidth / 2 - 440 / 2)/12 + "rem";
    },
    clickItem(index){
     
      this.SELETE_SHOW_DATA_ID=index;
      if(this.SELETE_SHOW_DATA_ID>=this.$store.state.randomDisplay.length){
        this.SELETE_SHOW_DATA_ID=this.$store.state.randomDisplay.length-1;
      }
      if(this.SELETE_SHOW_DATA_ID<0){
        this.SELETE_SHOW_DATA_ID=0;
      }
    },
    clickLeft(){
      this.SELETE_SHOW_DATA_ID++;
      if(this.SELETE_SHOW_DATA_ID>=this.$store.state.randomDisplay.length){
        this.SELETE_SHOW_DATA_ID=this.$store.state.randomDisplay.length-1;
      }
    },
     clickRight(){
      this.SELETE_SHOW_DATA_ID--;
      if(this.SELETE_SHOW_DATA_ID<0){
        this.SELETE_SHOW_DATA_ID=0;
      }
    }
  },
  created() {
    window.addEventListener("resize", this.GetWindowInfo); //注册监听器
    this.GetWindowInfo(); //页面创建时先调用一次
  },
};
import { HTML_HOME_INFORMATION } from "@/util/constants";
</script>

