<template>
  <div class="qa">
    <div>
      <div class="qa_background">
        <div class="qa_title">{{HTML_HOME_QA[$store.state.language].TITLE}}</div>
      </div>
      <div class="qa_list">
        <div
          class="qa_list_item"
          v-for="(item, index) in HTML_HOME_QA[$store.state.language].LIST"
          :key="index"
          @click="clickItem(index)"
        >
          <div
            :class="
              openList.indexOf(index) == -1
                ? 'qa_list_item_head_noselect'
                : 'qa_list_item_head'
            "
          >
            {{item.TITLE}}
          </div>
          <div
            :class="
              openList.indexOf(index) == -1
                ? 'qa_list_item_body_noselect'
                : 'qa_list_item_body'
            "

            
          >
            {{item.BODY}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QA", //问答
  computed: {},
  data() {
    return {
      HTML_HOME_QA,
      openList: [],
    };
  },
  watch: {},
  methods: {
    clickItem(index){
      if(this.openList.indexOf(index) == -1){
        this.openList.push(index);
      }else{
        this.openList=this.openList.filter(function(item){
          return item != index;
        });
      }
    }
  },
};
import { HTML_HOME_QA } from "@/util/constants";
</script>

